<template>
  <div id="contact" class="container">
    <h2>Contactgegevens</h2>
    <p>
      <i class="fa fa-envelope" aria-hidden="true"></i>&nbsp;&nbsp;
      info@glow-up.be
    </p>
    <p>
      <i class="fa fa-phone" aria-hidden="true"></i>&nbsp;&nbsp; 0485/10.85.39
    </p>
    <p>
      <i class="fa fa-facebook">&nbsp;</i>
      <a
        href="https://www.instagram.com/glowupbelgium/?utm_source=ig_embed"
        target="_blank"
        style="color: #eee"
      >Glow Up</a>
    </p>
    <h2>Afspraak maken</h2>
    <app-online-afspraak></app-online-afspraak>

    <p class="underline">
      <u>Afspraakuren</u>
      <br />Dinsdag: 8u30 tot 17u30
      <br />Woensdag: 8u30 tot 17u30
      <br />Donderdag: 11u tot 21u
      <br />Vrijdag: 8u30 tot 17u30
      <br />Zaterdag:
      8u30 tot 13u
      <br />
    </p>
    <app-contact-form></app-contact-form>
    <br />
    <hr class="hrtje" />
    <br />
    <div id="maps">
      <div class="card" id="bekijkmaps">
        <div class="card-body">
          <h3>
            Adres:
            <br />
          </h3>
          <p>
            Stationsstraat 37
            <br />9930 Lievegem, Belgie
          </p>
          <a
            id="bekijk"
            href="https://www.google.be/maps/place/Stationsstraat+37,+9950+Lievegem/@51.1519701,3.6071138,17z/data=!4m5!3m4!1s0x47c36509329819e7:0x35a42e28c0e11d14!8m2!3d51.1519701!4d3.6093078"
            target="_blank"
          >
            <h5>Bekijk op Google Maps</h5>
          </a>
        </div>
      </div>
      <!--<img v-if="mobile" src="../assets/adresmaps_mobile.png" style="width:100%" />-->
      <app-maps-contact :width="widthMap" :height="heightMap"></app-maps-contact>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import ContactFormVue from "../components/Contact/ContactForm.vue";
import MapsContactVue from "../components/Contact/MapsContact.vue";
import OnlineAfspraakVue from "../components/Contact/OnlineAfspraak.vue";

export default {
  name: "contact",
  data() {
    return {
      width: 0,
      widthMap: 600,
      heightMap: 450
    };
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    handleResize() {
      this.width = window.innerWidth;
      if (this.width < 950) {
        (this.widthMap = 320), (this.heightMap = 240);
      } else {
        (this.widthMap = 600), (this.heightMap = 450);
      }
    }
  },
  components: {
    appContactForm: ContactFormVue,
    appMapsContact: MapsContactVue,
    appOnlineAfspraak: OnlineAfspraakVue
  }
};
</script>

