<template>
  <div id="contactForm">
    <u class="underline">Verstuur een email</u>
    <form id="sendemail">
      <div class="form-group row">
        <label for="inputEmail3" class="col-sm-3 col-form-label">Email</label>
        <div class="col-sm-9">
          <input
            type="email"
            class="form-control"
            name="inputEmail3"
            v-model="form.email"
            placeholder="Email"
          />
        </div>
      </div>
      <div class="form-group row">
        <label for="inputName" class="col-sm-3 col-form-label">Naam</label>
        <div class="col-sm-9">
          <input
            type="text"
            class="form-control"
            name="inputName"
            v-model="form.naam"
            placeholder="An de Smet"
          />
        </div>
      </div>
      <div class="form-group row">
        <label for="inputNummer" class="col-sm-3 col-form-label">GSM/Tel</label>
        <div class="col-sm-9">
          <input
            type="text"
            class="form-control"
            name="inputName"
            v-model="form.nummer"
            placeholder="0499440077"
          />
        </div>
      </div>
      <div class="form-group row">
        <label for="inputText" class="col-sm-12 col-form-label"
          >Stel hier uw vraag</label
        >
        <div class="col-sm-12">
          <textarea
            class="form-control"
            name="inputText"
            v-model="form.tekst"
            rows="3"
          />
        </div>
      </div>
      <button @click="submit" class="btn btn-light">
        Verzend
      </button>
      <br />
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        naam: "",
        nummer: "",
        email: "",
        tekst: ""
      }
    };
  },
  methods: {
    submit(e) {
      e.preventDefault();
      Email.send({
        SecureToken: " b76cccb5-b626-475e-9ee1-cc35787f50d5",
        To: "arno_stas@hotmail.com",
        From: this.form.email,
        Subject: `Afspraak ${this.form.naam} - ${this.form.nummer}`,
        Body: this.form.tekst
      }).then(message => alert(message));
    }
  }
};
</script>

<style scoped>
#contactForm label {
  color: #eee;
  text-align: start !important;
}

.btn {
  float: left;
}
</style>
